.lesson_page{
    width: 100%;
    padding: 20px;
    margin-top: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.lp_container{
    width: 60%;
    padding: 10px;
}

.addLessonContainer{
    width: 100%;
    padding: 20px;
}

.buttonAddLesson{
    width: 20%;
    height: 40px;
    box-shadow: 0px 0px 1px 1px black;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: black;
}

.addLessonContainer :hover{
    transform: scale(1.1);
    background-color: rgb(241, 241, 241);
}

.refAddLesson{
    text-decoration: none;
}

.profileCourses{
    width: 100%;
    padding: 20px;
    display: flex;
}

.profileCoursesPhoto{
    width: 100px;
    height: 100px;
    border-radius: 100px;
    background-color: white;
    overflow: hidden;
}

.profileInfo{
    width: 50%;
    height: 100px;
    background-color: white;
    margin-left: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.profileInfoItems{
    color: gray;
    font-size: 12px;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.profileInfoImage{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.lessonsList{
    width: 100%;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: rgb(241, 240, 240);
}