.cq{
    width: 100%;
    padding: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cq_container{
    width: 60%;
    padding: 20px;
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}

.question{
    width: 60%;
    height: 25px;
    margin-top: 25px;
}

.question_photo{
    margin-top: 15px;
}

.answersTitle{
    margin-top: 20px;
}

.answer{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.result{
    width: 20px;
    height: 20px;
    margin-top: 24px;
    margin-left: 25px;
}

.button_setQuestion{
    width: 100px;
    height: 35px;
    margin-top: 25px;
}
