.header{

}

.container{
    width: 100%;
    max-width: 1100px;
    margin: 0 auto;
}

.header_inner{
    display: flex;
    justify-content: space-between;
    border-bottom: black 1px solid;
    padding: 20px 0;
    align-items: center;
}

.nav{
    display: flex;
    font-size: 13px;
    font-weight: 800;
    text-transform: uppercase;
}

.nav_link{
    color: black;
    text-decoration: none;
    opacity: 0.75;
    transition: opacity 0.2s linear;
    margin-left: 25px;
}

.nav_link:hover{
    opacity: 1;
}

.nav_link:first-child{
    margin-left: 0;
}