.textVideoLesson{
    width: 100%;
    height: 200px;
    font-family: 'Courier New', Courier, monospace;
    padding: 10px;
    resize: none;
    background-color: #f7f7f7;
    border-radius: 15px;
    border: none;
    margin-bottom: 10px;
}

.textVideoLesson:focus{
    background-color: #cfedfa;
}

.percentLesson{
    margin-top: 10px;
    height: 20px;
    background-color: aqua;
    display: flex;
    align-items: center;
    justify-content: center;
    transition:width 0.5s linear ;
}

.percentText{
    font-size: 12px;
}

.goBack{
    margin-bottom: 20px;
}