.cp_container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.cp_second_container{
    width: 70%;
    padding: 20px;
    /*background-color: yellow;*/
}
.addCourseButton{
    width: 20%;
    height: 40px;
    box-shadow: 0px 0px 2px 1px black;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 10%;
}

.addCourseButton:hover{
    width: 22%;
    transition: width 0.2s linear;
}

.addNavigation{
    text-decoration: none;
    color: black;
}

.allPostsContainer{
    width: 100%;
    margin-top: 20px;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: rgb(247, 245, 245);
}

.CourseLoader{
    width: 50px;
    height: 50px;
    margin-top: 50px;
    margin-bottom: 50px;
}

.navigation-bar{
    width: 400px;
    height: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.navigation-bar div:hover{
    color: blue;
    text-decoration: underline;
    cursor: pointer;
}


