.cl_container{
    width: 70%;
    height: 110px;
    box-shadow: 0px 0px 1px 1px black;
    margin-top: 20px;
    margin-bottom: 10px;
    overflow: hidden;
    display: flex;
    flex-direction: row;
    text-decoration: none;
    cursor: pointer;
}

.cl_container:hover{
    transform: scale(1.01);
    box-shadow: 0px 0px 10px 1px black;
    cursor: pointer;
    text-decoration: none;
}

.authorName{
    font-size: 10px;
    color: gray;
}

.data{
    font-size: 10px;
    color: gray;
}

.textContainer{
    display: flex;
    flex-direction: column;
    flex: 1;
    align-items: flex-start;
}

.imgContainer{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    background-color: white;
}

.imgForm{
    width: 90px;
    height: 90px;
    border-radius: 100px;
    background-color: aqua;
    margin-right: 5%;
    overflow: hidden;
}

.authorImage{
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.deleteButtonCourse{
    width: 10%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: beige;
    cursor: pointer;
}

.deleteButtonCourse:hover{
    width: 12%;
    background-color: tomato;
    transition: width 0.2s linear;
    cursor: pointer;
}

.navigationContainer{
    flex: 1;
    display: flex;
    flex-direction: row;
    text-decoration: none;
}