.showPostConatainer{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 20px;
}

.showPost{
    width: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.pictureBox{
    width: 90%;
    height: 80vh;
}

.picture{
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.textBox{
    width: 90%;
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.titlePost{
    font-size: 24px;
    font-weight: bold;
    text-align: center;
    margin-bottom: 20px;
}

.imgLoader{
    width: 50px;
    height: 50px;
    margin-top: 20%;
}

.textPost{
    font-size: 18px;
}