
.PF{
    background-color: white;
    box-shadow: 0px 0px 2px black;    
    margin-top: 30px;
    display: flex;
}

.PF:hover{   
    transform: scale(1.01);
    box-shadow: 0px 0px 4px black;
    cursor: pointer;
}

.textContainer{
    flex: 5;
    background-color: white;
    align-items: center;
    justify-content: center;
    padding: 15px;
    text-decoration: none;
    color: black;
}
.button_delete{
    flex: 0.2;
    display: flex;
    align-items: center;
    justify-content: center;
}
.img_button{
    width: 15px;
    height: 20px;
}

.PFContainer{
    display: flex;
    flex: 1;
    flex-direction: row;
}

.button_delete:hover{
    background-color: #e66572;
    cursor: pointer;
}
