.acp{
    padding: 20px;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.acp_container{
    width: 60%;
    padding: 20px;
    display: flex;
    flex-direction: column;
}

.acp_container input{
    margin-top: -1px;
}

.inputContainer{
    width: 100%;
    padding: 20px;
    margin-top: 10px;
}
.addPhoto{
    margin-bottom: 20px;
    height: 4em;
    text-align: center;
    float: left;
}

.inputContainer button{
    height: 35px;
    margin-left: 0;
}

.nameImg{
    font-size: 14px;
    margin-bottom: 20px;
    color: gray;
    float: left;
    margin-left: 2%;
    line-height: 50px;
}