.lessonsList_Container{
    width: 80%;
    height: 50px;
    box-shadow: 0px 0px 1px 1px black;
    margin-top: 20px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    text-decoration: none;
    color: black;
    cursor: pointer;
    background-color: white;
}

.lessonsList_Container :hover{
    color: brown;
}

.deleteButtonLesson{
    width: 10%;
    background-color: red;
}

.titleLesson{
    flex: 1;
    display: flex;
    align-items: center;
    padding-left: 10px;
}

.lessonsList_Container2{
    display: flex;
    flex: 1;
    flex-direction: row;
    text-decoration: none;
    color: black;
    cursor: pointer;
    background-color: white;
}
