.ls{
    width: 100%;
    padding: 20px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.ls_container{
    width: 60%;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.video_container{
    width: 70%;
    height: 330px;
}

.lessonText_container{
    width: 80%;
    padding: 20px;
    background-color: #e3dfde;
    margin-top: 20px;
}

.navigationLS{
    width: 50%;
    padding: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.navigationLS div:hover{
    color: blue;
    cursor: pointer;
    text-decoration: underline;
}