.cont{
    width: 100%;
    background-color: white;
}
.Container{
    width: 55%;
    padding: 10px;
    margin: 0 auto;
    margin-top: 25px;
    background-color: white;
}


.titleContainer{
    font-size: 25px;
    font-weight: bolder;
    margin-bottom: 30px;
    text-align: center;
}
.numberPage{
    margin: 15px;
    margin-top: 30px;
    text-align: center;
}

.numberPage:hover{
    color: blue;
    text-decoration:underline;
    text-decoration-color: blue;
    cursor: pointer;
}

.addPostAreas{
    width: 100%;
    height: 50vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-bottom: 50px;
}
.titel_area{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}
.title_input{
    width: 100%;
    height: 30px;
    border: none;
    padding-left: 10px;
    background-color: #f7f7f7;
    margin-top: 30px;
    margin-bottom: 20px;
}

.title_input:hover{
    background-color: #cfedfa;
    border-radius: 50px;
    transition: border-radius 1s linear;
    color: black;
}

.title_input:focus{
    background-color: #cfedfa;
    border-radius: 20px;
    color: black;
    transition: border-radius 0.5s linear;   
}


.fiel_area{
    flex: 1;
    display: flex;
}
.text_area{
    flex: 4;
    align-items: center;
    justify-content: center;
}
.text_input{
    width: 100%;
    height: 150px;
    resize: none;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 14px;
    padding: 10px;
    background-color: #f7f7f7;
    border: none;
    margin-bottom: 5px;
}

.text_input:hover{
    background-color: #cfedfa;
    border-radius: 25px;
    transition: border-radius 0.5s linear;
    color: black;
}

.text_input:focus{
    border-radius: 15px;
    transition: border-radius 0.5s linear;
    color: black;
    background-color: #cfedfa;
}



input[type='file']{
    display: none;
    cursor: pointer;
}

label{
    cursor: pointer;
    width: 20%;
    height: 35px;
    display: flex;
    color: #7d7d7d;
    font-size: 13px;
    align-items: center;
    justify-content: center;
    background-color: #f7f7f7;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

label:hover{
    background-color: #cfedfa;
    border-radius: 50px;
    transition: border-radius 1s linear;
    color: white;
}

.button_add_post{
    cursor: pointer;
    color: #7d7d7d;
    margin-left: 10px;
    width: 15%;
    border: none;
    background-color: #f7f7f7;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.button_add_post:hover{
    background-color: #cfedfa;
    border-radius: 50px;
    transition: border-radius 1s linear;
    color: white;
}

.progressBar{
    height: 35px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: aqua;
    margin-left: 4%;
    transition: width 1s linear;
}

.attention{
    color: black;
    width: 100%;
    margin-top: 15px;
}

.namePicture{
    color: #a0a0a0;
    font-size: 12px;
}

.imageToogle{
    width: 50px;
    height: 50px;
}

.postsLoader{
    width: 5%;
    height: auto;
    text-align: center;
}

.loaderBox{
    display: flex;
    flex: 1;
    align-items: center;
    justify-content: center;
}

.selectPage{
    display: flex;
    flex: 1;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

textarea:focus, input:focus
{
  outline: none;
}